.MuiSvgIcon-root {
    cursor: pointer;
}

.fancy-btn {
    color: #ffffff !important;
    border-width: 0px !important;
    border-radius: 10px;
    font-size: 15px;
    padding: 0.8rem 2rem;
    font-family: 'PoppinsBold', Helvetica, Arial, Lucida, sans-serif !important;
    background: linear-gradient(90deg, #1055ff 0%, #e91286 100%);
    cursor: pointer;
    &:hover {
        background: #1055ff;
    }
}

.image-box {
    position: relative;
    margin: auto;
    overflow: hidden;
    width: auto;
    cursor: pointer;
}
.image-box img {
    max-width: 100%;
    transition: all 0.9s;
    display: block;
    width: 100%;
    height: auto;
    transform: scale(1);
}

.image-box:hover img {
    transform: scale(1.1);
}
