/* Global styles for the app */
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');

@import './components/';
@import './views/';

:root {
    --theme-font: 'Lato', sans-serif;
    --body-bg: #e6e8ea;
    --primary: #0099ff;
    --coal: #1a2733;
    --bg-white: #ffffff;
    --modal-hr: #d8d8d8;
    --modal-backdrop: #88c2f5;
    --bg-black: #000000;
}
html {
    scroll-behavior: smooth;
}
body {
    margin: 0;
    font-family: var(--theme-font);
    background-color: var(--body-bg);
}
img {
    cursor: pointer;
}
.headings::after {
    background-color: #6610d1;
    bottom: -15px;
    height: 5px;
    width: 60px;
    position: relative;
    content: '';
    display: block;
    transition: font-size 0.3s, color 0.6s;
}

.w-100 {
    width: 100%;
}
.userImg {
    transition: width 2s, height 2s, transform 2s;
    cursor: pointer;
    &:hover {
        width: auto;
        height: auto;
        transform: rotate(360deg);
    }
}

// -----------------global Background Colors-----------------//

.bg-white {
    background-color: var(--bg-white);
}
.bg-primary {
    background-color: var(--primary);
}

p {
    color: var(--coal);
    font-family: Lato;
    margin: 0;
    font-size: 14px;
    letter-spacing: 0;
    line-height: 18px;
}

// -----------------Page Title------------------ //
.pagetile-p {
    width: 400px;
    opacity: 0.5;
    color: #1a2733;
    font-family: Lato;
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 0;
    line-height: 24px;
    text-align: right;
}

.top-boxes {
    padding: 16px;
    border-radius: 8px;
    background-color: #ffffff;
    box-shadow: 1px 1px 3px 3px #c9d8ff;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    &:hover {
        background-image: linear-gradient(90deg, #1055ff 0%, #e91286 100%);
        transform: scale(1.1);
    }
}
.top-boxes:hover .h4Active {
    color: white !important;
    fill: #ffffff;
}

.user-detail-section {
    background-image: url(https://www.elegantthemes.com/layouts/wp-content/uploads/2018/05/marketing-bg6.png);
    background-repeat: no-repeat;
}
.Box-Short {
    color: transparent;
    transition: width 2s, height 2s, transform 2s;
    cursor: col-resize;
    padding: 1rem;
    border-radius: 1rem;
    box-shadow: 1px 1px 3px 3px #c9d8ff;
    position: relative;
    &::before {
        content: '';
        position: absolute;
        top: 60px;
        left: -30px;
        z-index: 1;
        border: solid 15px transparent;
        border-right-color: #c9d8ff;
    }
}
.user-project {
    background: white;
    border-radius: 1rem;
    padding: 1.5rem;
    height: auto;
    width: 400px;
    top: 3rem;
    right: 0rem;
    position: absolute;
    box-shadow: 1px 1px 10px #c9d8ff;
    transition: all 0.5s ease-in-out;
    border-left: 5px solid #1055ff;

    &:hover {
        border-left: 5px solid #1055ff;
    }
}
.project-bg-image {
    border-radius: 1rem;
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: #1055ff;
    box-shadow: 1px 1px 10px #d8d8d8;
    height: 100%;
}
.user-project p {
    margin-top: 0.6rem !important;
}
.name {
    color: orange;
    font-size: 23px;
}

// ----------------- Contact Us ------------------ //@at-root
.contactUs-rightSide {
    background-color: #6610d1;
    background: radial-gradient(circle at 55% 92%, #6610d1 0 12%, transparent 12.2%),
        radial-gradient(circle at 94% 72%, #426691 0 10%, transparent 10.2%),
        radial-gradient(circle at 20% max(78%, 350px), #6610d1 0 7%, transparent 7.2%),
        radial-gradient(circle at 0% 0%, #6610d1 0 40%, transparent 40.2%), #1f2e43;
    border-radius: 10px 0 0 10px;
}

// Grid Control
@media (min-width: 1200px) {
    .css-1oqqzyl-MuiContainer-root {
        max-width: 1260px;
    }
}
@media (max-width: 1020px) {
    .user-project {
        width: auto !important;
    }
}
